import React, { ComponentProps } from 'react';
import { Typography, makeStyles, createStyles } from '@material-ui/core';
import { Section, SectionFigure, ProjectCard } from 'src/components';
import useContent, { Content } from 'src/content/useContent';
import { BigTriangle, SmallTriangle, TriangleLine } from './shapes';
import { kickstarter, login, onboarding, parkingMeter } from './project-images';

type Props = {
  id: string;
};

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      projects: {
        marginBottom: theme.spacing(20),
      },

      projectContainer: {
        marginTop: theme.spacing(6),
      },

      section: {
        paddingTop: theme.spacing(11),
        paddingBottom: theme.spacing(5),
      },

      smallTriangle: {
        top: theme.spacing(-5),
        left: theme.spacing(-4),
      },

      bigTriangle: {
        top: 0,
        left: theme.spacing(1),
      },

      line: {
        top: theme.spacing(0.5),
        left: theme.spacing(-1),
      },
    }),
  { name: 'RecentWorkSection' },
);

type Project = Omit<ComponentProps<typeof ProjectCard>, 'children'>;

const projects = (content: Content): Project[] => [
  {
    ...content.recentWork.projects.mobileAppOnboarding,
    slug: 'mobile-app-onboarding',
    image: { src: onboarding },
  },
  {
    ...content.recentWork.projects.parkingMeter,
    slug: 'parking-meter',
    image: { src: parkingMeter },
  },
  {
    ...content.recentWork.projects.loginRedesign,
    slug: 'login-redesign',
    image: { src: login },
  },
  {
    ...content.recentWork.projects.kickstarterLanding,
    slug: 'kickstarter-landing',
    image: {
      src: kickstarter,
    },
  },
];

export const RecentWorkSection: React.FC<Props> = ({ id }) => {
  const classes = useStyles();
  const content = useContent();

  return (
    <>
      <Section
        id={id}
        title={content.recentWork.title}
        classes={{ section: classes.section }}
      >
        <Typography component="p">{content.recentWork.subtitle}</Typography>

        <SectionFigure
          figure={SmallTriangle}
          classes={{ element: classes.smallTriangle }}
        />
        <SectionFigure
          figure={BigTriangle}
          classes={{ element: classes.bigTriangle }}
        />
        <SectionFigure
          animated="stroke-draw"
          figure={TriangleLine}
          classes={{ element: classes.line }}
        />
      </Section>

      <div className={classes.projects}>
        {projects(content).map((project) => (
          <div key={project.title} className={classes.projectContainer}>
            <ProjectCard {...project} />
          </div>
        ))}
      </div>
    </>
  );
};
