import React from 'react';
import { Typography, Link, makeStyles, createStyles } from '@material-ui/core';
import { Section, SectionFigure } from 'src/components';
import useContent from 'src/content/useContent';
import { Circle, Line, Rectangle } from './shapes';
import { CV } from './files';

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      experienceParagraph: {
        marginTop: theme.spacing(2),
      },

      experienceFooter: {
        marginTop: theme.spacing(2),
      },

      resumeContainer: {
        marginTop: theme.spacing(1),
      },

      section: {
        paddingTop: theme.spacing(15),
      },

      circle: {
        top: theme.spacing(1),
        left: theme.spacing(10),
      },

      rectangle: {
        top: theme.spacing(-5),
        left: theme.spacing(2),
      },

      line: {
        top: theme.spacing(-6),
        left: theme.spacing(3),
      },
    }),
  {
    name: 'ExperienceSection',
  },
);

type Props = {
  id: string;
};

export const ExperienceSection: React.FC<Props> = ({ id }) => {
  const classes = useStyles();
  const content = useContent();

  return (
    <Section
      id={id}
      title={content.experience.title}
      classes={{ section: classes.section }}
    >
      {content.experience.paragraphs.map((paragraph, index) => (
        <Typography
          component="p"
          className={classes.experienceParagraph}
          key={index}
        >
          {paragraph}
        </Typography>
      ))}

      <div className={classes.experienceFooter}>
        <Typography component="p" variant="caption">
          {content.experience.caption}
        </Typography>
        <div className={classes.resumeContainer}>
          <Typography color="primary" variant="caption">
            <Link href={CV} target="_blank">
              {content.experience.resume}
            </Link>
          </Typography>
        </div>
      </div>

      <SectionFigure figure={Circle} classes={{ element: classes.circle }} />
      <SectionFigure
        figure={Rectangle}
        classes={{ element: classes.rectangle }}
      />
      <SectionFigure
        animated="stroke-draw"
        figure={Line}
        classes={{ element: classes.line }}
      />
    </Section>
  );
};
