import React from 'react';
import { Link, Typography, makeStyles, createStyles } from '@material-ui/core';
import { Section, SectionFigure } from 'src/components';
import useContent from 'src/content/useContent';
import { Rectangle, Ellipse, EllipseLine } from './shapes';

type Props = {
  id: string;
};

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      section: {
        paddingTop: theme.spacing(5),
        paddingBottom: theme.spacing(17),
      },

      rectangle: {
        top: 0,
        left: theme.spacing(6),
      },

      ellipse: {
        bottom: 0,
        left: theme.spacing(-5),
      },

      ellipseLine: {
        left: 0,
        bottom: theme.spacing(5),
      },
    }),
  { name: 'ContactSection' },
);

export const ContactSection: React.FC<Props> = ({ id }) => {
  const classes = useStyles();
  const content = useContent();

  return (
    <Section
      id={id}
      title={content.contact.title}
      classes={{ section: classes.section }}
    >
      {content.contact
        .content({
          mail: (
            <Typography
              color="primary"
              variant="caption"
              component={Link}
              href={`mailto:${content.contact.mail}`}
              target="_blank"
            >
              {content.contact.mail}
            </Typography>
          ),
          linkedin: (
            <Typography
              color="primary"
              variant="caption"
              component={Link}
              href={content.contact.linkedinUrl}
              target="_blank"
            >
              {content.contact.linkedin}
            </Typography>
          ),
        })
        .map((line, i) => (
          <Typography component="p" key={i}>
            {line}
          </Typography>
        ))}

      <SectionFigure
        figure={Rectangle}
        classes={{ element: classes.rectangle }}
      />
      <SectionFigure figure={Ellipse} classes={{ element: classes.ellipse }} />
      <SectionFigure
        animated="stroke-draw"
        figure={EllipseLine}
        classes={{ element: classes.ellipseLine }}
      />
    </Section>
  );
};
