import React from 'react';
import Loadable from 'react-loadable';
import { CircularProgress, Grid } from '@material-ui/core';

const Loading = () => (
  <Grid container justify="center">
    <Grid item>
      <CircularProgress color="primary" />
    </Grid>
  </Grid>
);

const options = {
  loading: Loading,
  delay: 400,
};

export const MobileAppOnboarding = Loadable({
  loader: () => import('./MobileAppOnboarding'),
  ...options,
});

export const ParkingMeter = Loadable({
  loader: () => import('./ParkingMeter'),
  ...options,
});

export const LoginRedesign = Loadable({
  loader: () => import('./LoginRedesign'),
  ...options,
});

export const KickstarterLanding = Loadable({
  loader: () => import('./KickstarterLanding'),
  ...options,
});
