import React from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import { useInView } from 'react-hook-inview';
import { Typography, makeStyles, Grid, createStyles } from '@material-ui/core';
import { ProjectSlug } from 'types';
import { Arrow, Line } from './figures';

const duration = 0.5;

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      link: {
        color: 'unset',
        textDecoration: 'none',
      },

      container: {
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(2),
        transition: `background-color 0.5s ease, color 0.5s ease`,

        '&:hover': {
          backgroundColor: '#F4F9FF',
          // color: theme.palette.common.white,
        },

        '&:hover $figure line': {
          // stroke: theme.palette.common.white,
        },

        '&:hover $figure path': {
          // fill: theme.palette.common.white,
        },
      },

      imageContainer: {
        width: '100%',
        paddingTop: '72.66%',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
      },

      contentContainer: {
        height: '100%',
      },

      contentItem: {
        display: 'flex',
        flexDirection: 'column',
        flex: 1,
      },

      content: {
        marginLeft: theme.spacing(8),
        width: theme.spacing(31),
      },

      title: {
        transform: 'translateY(-50%)',
      },

      description: {
        marginTop: theme.spacing(2),
      },

      relative: {
        position: 'relative',
      },

      figure: {
        position: 'absolute',
        opacity: 0,

        '&$visible': {
          transform: 'translateX(50%)',
          opacity: 1,
        },
      },

      line: {
        top: '49.7%',
        right: 0,
        transition: `transform ${duration}s ease, opacity ${duration}s ease`,

        '& line': {
          stroke: theme.palette.common.black,
          transition: `stroke 0.5s ease`,
        },
      },

      arrow: {
        bottom: theme.spacing(2),
        right: theme.spacing(10),
        transition: `transform ${duration}s ease ${duration}s, opacity ${duration}s ease ${duration}s`,

        '& path': {
          fill: theme.palette.common.black,
          transition: `fill 0.5s ease`,
        },
      },

      visible: {},
    }),
  { name: 'ProjectCard' },
);

type Props = {
  title: string;
  description: string;
  slug: ProjectSlug;
  image: {
    src: string;
  };
};

export const ProjectCard: React.FC<Props> = ({
  title,
  description,
  slug,
  image,
}) => {
  const classes = useStyles();
  const [lineRef, lineInView] = useInView();
  const [arrowRef, arrowInView] = useInView();

  const inView = lineInView && arrowInView;

  return (
    <Link to={`/${slug}`} className={classNames(classes.link)}>
      <div className={classes.container}>
        <Grid container>
          <Grid item xs={6}>
            <div
              className={classNames(classes.relative, classes.imageContainer)}
              style={{ backgroundImage: `url(${image.src})` }}
            >
              <Line
                ref={lineRef}
                className={classNames(classes.figure, classes.line, {
                  [classes.visible]: inView,
                })}
              />
            </div>
          </Grid>
          <Grid item xs={6} className={classes.relative}>
            <Grid
              container
              direction="column"
              className={classes.contentContainer}
            >
              <Grid item className={classes.contentItem}></Grid>
              <Grid
                item
                className={classNames(classes.contentItem, classes.content)}
              >
                <Typography
                  variant="h3"
                  component="h3"
                  className={classes.title}
                >
                  {title}
                </Typography>
                <Typography component="p">{description}</Typography>
              </Grid>
            </Grid>

            <Arrow
              ref={arrowRef}
              className={classNames(classes.figure, classes.arrow, {
                [classes.visible]: inView,
              })}
            />
          </Grid>
        </Grid>
      </div>
    </Link>
  );
};
